.raiseAds{width:407px;height:200px;padding:25px;margin-bottom:30px;display:flex;flex-direction:column;justify-content:space-between;background:linear-gradient(270deg, #6540cd 0%, #7e6af3 100%);box-shadow:0px 10px 40px rgba(102,78,249,.1);border-radius:10px}.flex{display:flex}.elips{width:70px;height:70px;margin-right:20px;display:flex;justify-content:center;align-items:center;background:#f8f8f8;box-shadow:0px 8px 15px rgba(0,0,0,.15);border-radius:50%;font-weight:700;font-size:25px;line-height:20px;color:#664ef9}.title{margin:0;font-weight:700;font-size:20px;line-height:20px;color:#fff}.remains{position:relative;display:flex;margin-top:5px;margin-bottom:7px;font-weight:600;font-size:14px;line-height:20px;color:#fff}.remains:before{display:block;content:"Осталось:";margin-right:5px;font-weight:500;font-size:14px;line-height:20px;color:rgba(255,255,255,.5)}.expires{position:relative;display:flex;font-weight:600;font-size:14px;line-height:17px;color:#fff}.expires:before{display:block;content:"Истекают через:";margin-right:5px;font-weight:500;font-size:14px;line-height:17px;color:rgba(255,255,255,.5)}.buttons{display:flex;justify-content:space-between}.buyBtn{display:flex;align-items:center;padding:10px 15px;background:#fff;border-radius:17px;font-weight:600;font-size:14px;line-height:17px;color:#664ef9}.moreBtn{display:flex;align-items:center;padding:10px 15px;background:rgba(255,255,255,.1);border-radius:17px;font-weight:600;font-size:14px;line-height:17px;color:#fff}